import { firebaseConfig as firebase } from "../firebaseConfig";

export const SetToLocalStorage = (email, password, history) => {
    AddToStorage(history, email)
}
export const fromVerificationPage = (email, password, history) => (e) => {
    e.preventDefault();
    AddToStorage(history, email)
}
export const AddToStorage = async (history, email) => {
    const uri = "http://localhost:3000" || "https://profilomaker-5a21c.web.app"
    var actionCodeSettings = {
        url: `https://profilomaker-5a21c.web.app/VerificationPage`,
        handleCodeInApp: true,
    };
    firebase.auth().sendSignInLinkToEmail(email, actionCodeSettings)
        .then(() => {
            window.localStorage.setItem('emailForSignIn', email);
            alert("Mail has been send to your email address! Please complete your verification")
            history.push("/VerificationPage")
        })
        .catch((error) => {
            var errorCode = error.code;
            var errorMessage = error.message;
            console.log(errorCode)
            console.log(errorMessage)
        });
}